@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import 'components/_fonts.scss';
.swiper {
    &-pagination {
        display: flex;
        justify-content: center;
        align-items: center;
        &-bullet {
            width: 14px;
            height: 3px;
            border-radius: 2px;
            background: #9F9C9C;
            margin: 0 5px !important;
            opacity: 1;
            &-active {
                background: $primary;
            }
        }
    }
}

.mfg {
    position: relative;
    margin: 0;
    width: 100%;
    select,
    input {
        border: none;
        border-bottom: 1px solid rgba(#A3A3A3, .5);
        padding: 18px 0;
        width: 100%;
        font-size: 18px;
        line-height: 166%;
        background-color: transparent;
        color: $black;
        &::placeholder {
            color: #999999;
        }
    }
    select {
        color: #999999;
        -webkit-appearance: none;
        -moz-appearance: none;
        -ms-appearance: none;
        -o-appearance: none;
        appearance: none;
        background-image: url(../img/select-arrow.svg);
        background-repeat: no-repeat;
        background-position: right 6px center;
        &::-ms-expand {
            display: none;
        }
    }
    .selected {
        color: $black;
    }
}

.file-input {
    width: 210px;
    height: 50px;
    position: relative;
    display: flex;
    align-items: center;
    padding: 0 11px;
    background-color: #F4F4F4;
    font-size: 14px;
    &:hover {
        background-color: darken(#f4f4f4, 5%);
    }
    svg {
        margin-right: 15px;
    }
    input {
        position: absolute;
        z-index: 2;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        cursor: pointer;
    }
}

.mcheck {
    position: relative;
    color: #707070;
    input {
        position: absolute;
        opacity: 0;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: 2;
        cursor: pointer;
        &:checked+label {
            &::before {
                background-repeat: no-repeat;
                background-position: center;
                background-image: url(../img/check.svg);
            }
        }
        &:disabled+label {
            color: #AAAAAA;
            pointer-events: none;
        }
    }
    label {
        padding-left: 30px;
        position: relative;
        min-height: 22px;
        font-size: 14px;
        line-height: 22px;
        cursor: pointer;
        display: block;
        &:before {
            content: '';
            position: absolute;
            z-index: 1;
            border: 1px solid #707070;
            width: 18px;
            height: 18px;
            left: 0;
            top: 3px;
        }
    }
    a {
        text-decoration: underline;
        position: relative;
        z-index: 3;
        &:hover {
            color: $primary;
        }
    }
}

.pagination {
    margin-top: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    &__item {
        height: 32px;
        min-width: 28px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0 6px;
        font-size: 20px;
        border-radius: 1px;
        &:hover,
        &--active {
            background-color: $primary;
            border-color: $primary;
            color: #fff;
        }
    }
    &__btn {
        &--prev {
            margin-right: 42px;
        }
        &--next {
            margin-left: 42px;
        }
        &:hover {
            path {
                stroke: $black;
            }
        }
    }
    &__item {
        margin: 0 3px;
        &--active {
            pointer-events: none;
        }
    }
    &__more {
        display: none;
    }
}

.slideToggle-block {
    &__hidden {
        height: 0;
        overflow: hidden;
        transition: all .425s;
    }
}

.search-field {
    position: relative;
    &__btn {
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
    }
    input {
        font-size: 14px;
        padding: 12px 0;
    }
}

@media screen and (max-width: 992px){
    .mfg{
        input{
            padding: 12px 0;
            font-size: 14px;
        }
    }

    .mcheck{
        label{
            font-size: 11px;
            line-height: 16px;
            &::before{
                top: 5px;
            }
        }
    }

    .file-input{
        height: 40px;
        width: 175px;
        font-size: 12px;
        svg{
            width: 16px;
            height: auto;
            margin-right: 6.8px;
        }
    }

    .pagination{
        display: none;
        &__more {
            display: flex;
            height: 51px;
            width: 500px;
            max-width: 100%;
            margin: 40px auto 0;
            font-size: 13px;
            font-weight: 500;
            text-transform: uppercase;
        }
    }
}
